<template>
  <div :class="['clip-box','tran-all-3','z-index-max',isOpen ? 'active' : '']">
    <div class="warp">
      <div id="clipArea"></div>

      <div class="opts">
        <div class="warp">
          <input type="button" value="取消" @click="cancel" class="left mg-l10"/>
          <input type="button" value="选取" id="clipBtn" class="right mg-r10" style="text-align:right;"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import $ from 'jquery'
  import PhotoClip from '../assets/js/jquery.photoClip'

  export default{
    ready(){
      this.initClip();
      this.$on('open-clip',function(open){
        this.isOpen = open;
      });
    },
    data(){
      return {
        picture:'',
        isOpen:false
      }
    },
    methods: {
      initClip(){
        let vm = this;
        let w = $(window).width();
        let h = $(window).height() - $(".opts").height();
        $('#clipArea').height(h);
        var clipArea = new PhotoClip("#clipArea", {
          size: [w, w],
          outputSize: [100, 100],
          // file: "#file",
          // view: "#view",
          ok: "#clipBtn",
          // loadStart: function() {
          //   console.log("照片读取中");
          // },
          // loadComplete: function() {
          //   console.log("照片读取完成");
          // },
          clipFinish: function(dataURL) {
            vm.$dispatch('over-clip',dataURL);
            vm.isOpen = false;
          }
        });
        this.$on('set-source',function(source){
          this.picture = source;
          clipArea.setSource(source);
          this.isOpen = true;
        });
        this.$once('destroy-clip',clipArea.destroy);
      },
      cancel(){
        this.isOpen = false;
      }
    },
    destroyed()
    {
      this.$emit('destroy-clip');
    },
    components: {}
  }
</script>
<style scoped>
  .clip-box{
    position: fixed;
    top: 0;
    left: 100%;
    right: 0;
    bottom: 0;
  }

  .clip-box.active{
    left: 0;
  }

  .clip-box .warp{
    position: relative;
    width: 100%;
    height: 100%;
  }

  #clipArea{
    background-color: #000;
    width: 100%;
  }

  .opts{
    height: .6rem;
  }

  .opts .warp{
    height: 100%;
    background-color: #0e0f0f;
    line-height: .6rem;
    font-size: .16rem;
    color: #fff;
  }

  .opts input[type='button']{
    text-align: left;
    width: 1rem;
    height: 100%;
    background-color: #0e0f0f;
  }
</style>




